import { BrowserModule } from '@angular/platform-browser';
import { NgModule, APP_INITIALIZER } from '@angular/core';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { AlHttpInterceptor } from './httpInterceptor';
import { environment } from '../environments/environment';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AppRoutingModule } from './app-routing.module';
import { SharedService } from './services/shared.service';
import { TranslateService } from './services/translate.service';
import { TranslatePipe } from './pipes/translate.pipe';
import { AppComponent } from './app.component';
import { CoreComponent } from './layouts/core/core.component';
import { ToolbarComponent } from './components/toolbar/toolbar.component';
import { HomeComponent } from './layouts/home/home.component';
import { LoaderComponent } from './components/loader/loader.component';
import { LabelPipe } from './pipes/label.pipe';
import { ArticlesComponent } from './components/articles/articles.component';
import { ActivitiesComponent } from './components/activities/activities.component';
import { NewsComponent } from './components/news/news.component';
import { ContactComponent } from './components/contact/contact.component';
import { AboutComponent } from './components/about/about.component';
import { FooterComponent } from './components/footer/footer.component';
import { ArticleBlockComponent } from './components/article-block/article-block.component';
import { CarriereComponent } from './components/carriere/carriere.component';
import { OffresComponent } from './components/offres/offres.component';

export function setupTranslateFactory(service: TranslateService): Function {
    return localStorage.getItem('flag')
        ? () => service.use(localStorage.getItem('flag'))
        : () => service.use('fr');
}



@NgModule({
    declarations: [
        AppComponent,
        CoreComponent,
        ToolbarComponent,
        FooterComponent,
        HomeComponent,
        TranslatePipe,
        LoaderComponent,
        LabelPipe,
        ArticlesComponent,
        ActivitiesComponent,
        NewsComponent,
        ContactComponent,
        AboutComponent,
        ArticleBlockComponent,
        CarriereComponent,
        OffresComponent
    ],
    imports: [
        BrowserModule,
        AppRoutingModule,
        ReactiveFormsModule,
        HttpClientModule,
        FormsModule,
        BrowserAnimationsModule, // required animations module
    ],
    providers: [
        SharedService,
        TranslateService,
        {
            provide: APP_INITIALIZER,
            useFactory: setupTranslateFactory,
            deps: [TranslateService],
            multi: true
        },
        {
            provide: HTTP_INTERCEPTORS,
            useClass: AlHttpInterceptor,
            multi: true
        }
    ],
    bootstrap: [AppComponent]
})
export class AppModule { }
