import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { environment } from '../../environments/environment';
import { first } from 'rxjs/internal/operators/first';

export interface Articles {
    id: string;
    name: string;
    description: string;
    prix: number;
    src: string;
    visible: number
}


@Injectable({
    providedIn: 'root'
})
export class HttpService {
    constructor(private http: HttpClient) { }

    public getArticles() {
        return this.http
            .get<Articles[]>(`${environment.apiUrl}/articles/read.php`)
            .pipe(map(res => res as any));
    }

    public sendMail(email) {
        console.log(email);
        return this.http.post<any>("/sendmail.php", email)
            .pipe(map(res => res as any));
    }


    // https://api.sendgrid.com/api/mail.send.json
    // POST Data	api_user=your_sendgrid_username&api_key=your_sendgrid_password&to=destination@example.com&toname=Destination&subject=Example_Subject&text=testingtextbody&from=info@domain.com
}
